@import './Layout.scss';

.about {
    flex: 1 1 auto;
}
.about__container {
}
.about__body {
    padding: 18px 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.about__title {
    margin: 5px 0;
    font-family: 'Rowdies', cursive;
    font-weight: 600;
    @include adaptivValue('font-size', 32, 26, 1);
    color: aliceblue;
}
.about__subtitle {
    margin: 5px 0;
    font-family: 'Rowdies', cursive;
    font-weight: 400;
    @include adaptivValue('font-size', 26, 18, 1);
    color: aliceblue;
}
.about__gamerules {
    margin: 10px 0;
}
.about__item {
    list-style-type: style;
    margin: 5px 0;
    font-family: 'Rowdies', cursive;
    font-weight: 400;
    @include adaptivValue('font-size', 18, 14, 1);
    color: aliceblue;
}

@media (max-width: 536px) {
    .about__item{
        list-style-type: none;
    }
}