@import '../styles/Layout.scss';

.contact {
    flex: 1 1 auto;
}
.contact__container {
}
.contact__body {
    display: flex;
    flex-direction: column;
    @include adaptivValue ('gap', 20, 10, 1);
}
.contact__title {
    @include adaptivValue ('margin-top', 32, 18, 1);
    font-family: 'Rowdies', cursive;
    font-weight: 400;
    @include adaptivValue ('font-size', 32, 18, 1);
    color: white;
    text-align: center;
}
.contact__contacts {
    margin: 0 auto;
    @include adaptivValue ('margin-top', 32, 18, 1);
}
.contact__list {
    display: flex;
    flex-direction: column;
    @include adaptivValue ('gap', 20, 10, 1);
}
.contact__item {
    display: flex;
    align-items: center;
    @include adaptivValue ('gap', 15, 8, 1);
}
.contact__icon {
    width: 50px;
    height: 50px;
}
.contact__link {
    font-family: 'Rowdies', cursive;
    font-weight: 400;
    @include adaptivValue ('font-size', 18, 12, 1);
    color: white;
    text-align: center;
    cursor: pointer;
}