@import './Layout.scss';

.game {
    flex: 1 1 auto;
}
.game__body {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    gap: 10px;
}
.game__descreption{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}
.game__title {
    @include adaptivValue ('margin-top', 22, 12, 1);
    font-family: 'Rowdies', cursive;
    font-weight: 400;
    font-size: 16px;
    color: white;
    text-align: center;
    cursor: pointer;
}
.game__counter {
    font-family: 'Rowdies', cursive;
    font-size: 16px;
    color: white;
    font-weight: 200;
    text-align: center;
    cursor: pointer;
    margin: 8px 0;
}
.game__tryagain{
    font-family: 'Rowdies', cursive;
    font-size: 16px;
    color: white;
    font-weight: 200;
    text-align: center;
    cursor: pointer;
    padding: 0 10px;
    border-radius: 10px;
    border: solid 1px white;
    cursor: pointer;
}
$color-btn-game__tryagain: #ffffff30;
$border-radius-btn-game__tryagain: 10px;
.game__tryagain{
    position: relative;
    z-index: 0;
}
.game__tryagain::before{
    position: absolute;
    content: "";
    background-color: $color-btn-game__tryagain;
    height: 100%;
    width: 0;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    transition: all .6s ease;
    border-radius: $border-radius-btn-game__tryagain;
    z-index: -1;
}
.game__tryagain:hover::before{
   width: 100%;
}
.game__card {
    width: 100%;
}
.card__field {
    width: fit-content;
    margin: 0 auto;
    padding: 0 5px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(3, 1fr);
    align-content: center;
    justify-items: center;

    text-justify: center;

    gap: 10px;
    position: relative;
}
.fillter{
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    height: 30%;
    transform: translateY(-57%) translateX(-50%);
    z-index: 50;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Rowdies', cursive;
    // @include adaptivValue('font-size', 110, 56, 1);
    font-size: 600%;
    color: rgba(255, 153, 0, 0.935);
    text-shadow: 0 0 1em rgb(0, 0, 0), 0 0 0.2em rgb(0, 0, 0);

}
.none{
    display: none;
}
.card {
    width: 150px;
    height: 150px;
    border-radius: 10px;
    border: solid white 5px;
    background-color: antiquewhite;
    position: relative;
    z-index: 1;
    transition: ease .3s all;
    transform: rotate3d(0, 1, 0, 0deg);
}
.card.rotate{
    transform: rotate3d(0, 1, 0, 90deg);
}
.card.close.rotate{
    transform: rotate3d(0, 1, 0, 0deg);
}
.card.close::after,
.card.close::before {
    border-radius: 10px;
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    transition: ease 3s all;
}
.matched{
    pointer-events: none;
}
.matched.close::after,
.matched.close::before{
    display: none;
}
.card.close::after {
    background-color: rgb(0, 87, 113);
    z-index: 2;
}
.card.close::before {
    background-image: url(../stuff/img/React-icon.svg.png);
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    z-index: 3;
}
.card__body {
    padding: 3px;
    width: 100%;
    height: 100%;
}
.card__img {
    width: 100%;
    height: 100%;
}

@media (max-width: 645px) {
    .card{
        width: 20vw;
        height: 20vw;
        border-radius: 5px;
        border: solid white 2px;
    }
    .card__field{
        gap: 3px;
    }
}
@media (max-width: 500px) {
    .fillter{
        font-size: 450%;
    }
}
@media (max-width: 360px) {
    .fillter{
        font-size: 350%;
    }
}
@media (max-width: 280px) {
    .fillter{
        font-size: 250%;
    }
}
@media (max-width: 190px) {
    .fillter{
        font-size: 190%;
    }
}