@use './null.css';

$maxWidth: 1920; // Масксимальная ширина полотна
$maxWidthContainer: 1170; // Масксимальная ширина ограничивающего контейнера

@mixin adaptivValue ($property, $maxSize, $minSize, $type) {
    $size: $maxSize - $minSize;
    @if $type==1{
        // Если меньше ограничивающего контейнера
        #{$property}: $maxSize + px;
        @media (max-width: #{$maxWidthContainer + px}) {
            #{$property}: calc(#{$minSize + px} + #{$size} * ((100vw - 320px)/#{$maxWidthContainer - 320}));
        }

    } @else if $type==2{
        // Если больше ограничивающего контейнера
        #{$property}: $maxSize + px;
        @media (max-width: #{$maxWidthContainer + px}) {
            #{$property}: calc(#{$minSize + px} + #{$size} * ((100vw - 320px)/#{$maxWidth - 320}));
        }
    } @else {
        // Всегда
        #{$property}: calc(#{$minSize + px} + #{$size} * ((100vw - 320px)/#{$maxWidth - 320}));
    }
}

body{
    background-color: rgb(50, 50, 50);
}

.wrapper {
    margin: 0 auto;
    height: 100%;
    overflow: hidden;
    display: flex;
    flex-direction: column;
}

._container{
    margin: 0 auto;
    padding: 0 5px;
    max-width: 1200px;
}

.header {
    box-shadow: 0px 1px 3px rgba(255, 255, 255, 0.557);
}
.header__container {
}
.header__body {
    display: block;
    width: 100%;
    height: 70px;
}

.header-nav {

}
.header-nav__body {
    height: 100%;
    align-items: flex-end;
}
.header-nav__list {
    height: 70px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 3%;
}
.header-nav__item {
    font-family: 'Rowdies', cursive;
    font-size: 16px;
    color: white;
    text-align: center;
    cursor: pointer;
}
.header-nav__link {
    color: white;
}
//HOVER-нижнее подчеркивание элемента
$color-header-nav__link: rgb(255, 255, 255);
$border-size-header-nav__link: 3px;
.header-nav__link{
    position: relative;
}
.header-nav__link::before{
    position: absolute;
    content: "";
    top: 50%;
    left: 50%;
    width: 0;
    height: 100%;
    transition: all .6s ease;
    border-bottom: solid $color-header-nav__link $border-size-header-nav__link;
    transform: translateX(-50%);
}
.header-nav__link:hover::before {
    width: 100%;
}
.footer {
    box-shadow: 0px -1px 3px rgba(255, 255, 255, 0.557);
}



.footer__container {
}
.footer__body {
    height: 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.footer__title {
    font-family: 'Rowdies', cursive;
    font-size: 12px;
    color: white;
    font-weight: 200;
    cursor: pointer;
    padding: 0 0 0 5px;
}

.footer-nav {

}
.footer-nav__body {
    padding: 0 5px 0 0;
}
.footer-nav__list {
    display: flex;
    gap: 1vw;
}
.footer-nav__item {
    font-family: 'Rowdies', cursive;
    font-size: 14px;
    color: white;
    text-align: center;
    cursor: pointer;
}
.footer-nav__link {
    color: white;
}
//HOVER-нижнее подчеркивание элемента
$color-footer-nav__link: rgb(255, 255, 255);
$border-size-footer-nav__link: 3px;
.footer-nav__link{
    position: relative;
}
.footer-nav__link::before{
    position: absolute;
    content: "";
    top: 50%;
    left: 50%;
    width: 0;
    height: 100%;
    transition: all .6s ease;
    border-bottom: solid $color-footer-nav__link $border-size-footer-nav__link;
    transform: translateX(-50%);
}
.footer-nav__link:hover::before {
    width: 100%;
}

#root{
    height: 100%;
}
@media (max-height: 570px) {
    #root{
        height: auto;
        overflow: scroll;
    }
}